import React from "react";
import styled, {css} from "styled-components";
import polygonImg from "../../assets/imgs/polygon@2x.png"
import ETHImg from "../../assets/imgs/ETH@2x.png"

import {useGetCurrency, useUpDataCurrency} from "../../state/CurrentCoin/hooks";

export const NetSelectWidget: React.FC = () => {
    const {currencyList,currency} = useGetCurrency()
    const upData = useUpDataCurrency()
    return (
        <NewSelectStyle>
            <EthWidget onClick={()=>{
                window.location.href="https://app.nsure.network/"
            }}>
                    <img src={ETHImg} alt=""/>
                    <div>
                        <h2>Ethereum</h2>
                        <h3>Market</h3>
                    </div>

            </EthWidget>


            {
                currencyList.map((ev)=>{
                    return (
                        <PolygonWapStyle key={ev.name} onClick={()=>upData(ev)}>
                            <LeftStyle className={`${ev.id == currency.id?"ac":"left"}`}>

                                <img src={ev.img} alt=""/>
                                <div>
                                    <h2>{ev.name}</h2>
                                    <h3>Market</h3>
                                </div>
                            </LeftStyle>
                            <RightStyle>
                                <img src={polygonImg} alt=""/>
                                <p>Polygon</p>
                            </RightStyle>

                        </PolygonWapStyle>
                    );
                })
            }

        </NewSelectStyle>
    );
}


const WapStyles = css`
  display: flex;
  background: #CED6FF;
  border-radius: 6px;
  overflow: hidden;
  cursor: pointer;

`

const LeftStyle =styled.div`
  display: flex;
  padding: 0 5px;
  align-items: center;

  img {
    display: block;
    margin-right: 10px;
    width: 28px;
  }

  div {
    padding-right: 20px;
    h2 {
      height: 25px;
      font-size: 18px;
      font-weight: 500;
      color: #270D4F;
      line-height: 25px;
      text-transform: Uppercase;

      font-family: PingFangSC-Medium, PingFang SC;
    }

    h3 {
      height: 17px;
      font-size: 12px;
      font-weight: 500;
      color: #9C97AC;

      font-family: PingFangSC-Medium, PingFang SC;
      line-height: 17px;
    }
  }
`


const EthWidget = styled(LeftStyle)`
  ${WapStyles};
  
`


const RightStyle = styled.div`
  text-align: center;
  background-color: #AA79E9;
  padding: 3px 11px;
  
 

  img {
    display: block;
    width: 28px;
    height: 28px;
    margin: 0 auto;
  }
  p{

    height: 17px;
    font-size: 12px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #FFFFFF;
    line-height: 17px;
  }
`

const PolygonWapStyle = styled.div`
  ${WapStyles};
  margin-left: 30px;
  
  .left{
    &:hover{
      background-color: #CED6FF;
      h2{
        color: #FFFFFF;
      }
    }
  }
  .ac{
    background: linear-gradient(90deg, #DF52E5 0%, #6FA0FF 100%);
    h2,h3{
      color: #FFFFFF;
    }
  }
  
 
`

const NewSelectStyle = styled.div`
  padding: 20px 30px;
  box-sizing: border-box;
  background-color: #fff;
  display: flex;
  justify-content: flex-end;
`
